.checkbox-item{
  label,
  label.ls-label-xs-visibility{
    padding-left: 18px;
    padding-top: 3px;

  &::before{
		width: $checkboxsize;
		height: $checkboxsize;
  }

  &::after{
    width: $checkboxsize;
    height: $checkboxsize;
    border-radius: 2px;
    padding-top: 0px;
    padding-left: 1px;
    background-color: $color1;
    color: white;
    font-size: 24px;
    line-height: 27px;
    }
  }
}

.checkbox-item .ls-label-xs-visibility{
  width: $checkboxsize;
  height: $checkboxsize;
}

.checkbox-list{
  .input-sm{
    height: $checkboxsize + 2; 
    font-size: inherit;
  }
}

.othertext-label-checkbox-container {
  padding-top: 0;
}