.radio-item{
  .ls-label-xs-visibility{
    width: $radiosize;
    height: $radiosize;
  }
  label{
    padding-left: 20px;
    padding-top: 3px;
    
    &:before{
      width: $radiosize;
      height: $radiosize;
      border-color: #aaa;
      margin-right: 20px;
      

    }
    
    &::after{
      background-color: $color1;
      width: $radiosize - 10;
      height: $radiosize - 10;
      left: 5px;
      top: 5px;
    }
  }
}

