
body {
  background-color: #eeeeee;
	background-image: url(../images/squaregrid.gif);
	background-repeat: repeat;
	background-position: fixed;
	font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
	font-size: $bodyfontsize;
	color: $textcolor;
}

.table > tbody > tr > td {
	vertical-align: top;  /* Overruling Bootstrap for checkboxes and radios, since Bootstrap aligns them at top. */
}

.panel-primary{
	border-color: $color1;
	
	.panel-heading{
		background: $color1;
		border-color: $color1;
		
		h4{
			color: white;
		}
	}
}

.checkbox-item label::before{
  border-color: #aaa;
}

.form-control{
  border-color: #aaa;
}