table{
	overflow: hidden;
}

.table-bordered,
.table-bordered > thead > tr > th, 
.table-bordered > tbody > tr > th, 
.table-bordered > tfoot > tr > th, 
.table-bordered > thead > tr > td, 
.table-bordered > tbody > tr > td, 
.table-bordered > tfoot > tr > td {
    border: none;	
}

.table,
.table > thead > tr > th, 
.table > tbody > tr > th, 
.table > tfoot > tr > th, 
.table > thead > tr > td, 
.table > tbody > tr > td, 
.table > tfoot > tr > td {
	border-top: 1px solid $bordercolor;
	border-bottom: 1px solid $bordercolor;
}

.table.table-col-hover,
.table.table-col-hover > thead > tr > th, 
.table.table-col-hover > tbody > tr > th, 
.table.table-col-hover > tfoot > tr > th, 
.table.table-col-hover > thead > tr > td, 
.table.table-col-hover > tbody > tr > td, 
.table.table-col-hover > tfoot > tr > td {
	border: none;
	border-right: 1px solid $bordercolor;
}

.table.table-col-hover > thead > tr > th{
	border-bottom: 1px solid $bordercolor;
}

.table-hover > tbody > tr:hover {
    background-color: #eee;
		
		td:hover{
			background-color: #ddd;
		}
}

.table.table-col-hover{
	td:hover{
		background: #ddd;
	}
}

.table-col-hover {
	td{
		border-right: 1px solid $bordercolor;
	}
	
	td:hover::after { 
    background-color: #ddd;
    content: '';  
    height: 10000px;    
    left: 0;
    position: absolute;  
    top: -5000px;
    width: 100%;
    z-index: -1;
		overflow: hidden;
	}
}