/* general styling of questions */

.question-container{
	border: 1px solid rgba(102, 102, 102, 0.35);
  background-color: rgba(255, 255, 255, 0.5);
  margin-bottom: 30px;
  border-radius: 4px;
	box-shadow: none;
	transition: box-shadow 0.5s;
	
	&:hover{
		box-shadow: 0 0 20px #888888;	
	}
}

.input-error.question-container{
	border-color: #b20d28;
  background-color: rgba(178, 13, 40, 0.05);
  box-shadow: 0 0 5px #b20d28;
	
	.question-title-container{
		background-color: #b20d28;
    background-color: #8f1a2c;
    background-image: -moz-linear-gradient(#b20d28, #8f1a2c);
    background-image: -webkit-gradient(linear, 0% 0%, 0% 100%, from(#b20d28), to(#8f1a2c));
    background-image: -webkit-linear-gradient(#b20d28, #8f1a2c);
    background-image: -o-linear-gradient(#b20d28, #8f1a2c);
		color: white;
    text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.5);
	}
}

td.total{
	border: none;
	background-color: transparent;

	label.sr-only{
		//border: none;
	}

	.form-control[disabled]{
		background: #bbb;
		border: none;
		box-shadow: none;
		border-radius: 0;
		color: #333;
	}
}


.question-title-container{
	color: $color1;
	text-shadow: 0 1px 0 white;
	display: flex;
	background-color: #eeeeee;
	background-color: #e2e2e2;
	background-image: -moz-linear-gradient(#eeeeee, #e2e2e2);
	background-image: -webkit-gradient(linear, 0% 0%, 0% 100%, from(#eeeeee), to(#e2e2e2));
	background-image: -webkit-linear-gradient(#eeeeee, #e2e2e2);
	background-image: -o-linear-gradient(#eeeeee, #e2e2e2);
	border-top-right-radius: 4px;
	border-top-left-radius: 4px;
	border-bottom-right-radius: 0px;
	border-bottom-left-radius: 0px;
	
	p:last-child{
		margin-bottom: 0;
	}
}

.ls-answers{
	padding-top:0;
	padding-bottom: 0;
	margin-top:15px;
	margin-bottom: 15px;
	
	.form-inline .form-group{
		//margin-bottom: 15px;
	}
	
	&.form-group{
		margin-bottom: 15px;
	}
	
	ul:first-child{
		margin-bottom: 0;
		
		li:last-child{
			//margin-bottom: 0;
		}
	}
}

.ls-question-help, .ls-questionhelp {
	font-size: 80%;
	color: darken($helpcolor, 10%);
	
	a{
		text-decoration: underline;
		color: darken($helpcolor, 10%);
	}
}

.asterisk {
	order: 1;
	width: 20px;
	font-size: 12px;
}

.ls-question-mandatory,
.ls-question-help{
	font-weight: normal;
	font-size: 80%;
	margin-top: 5px;
	margin-left: 4px;
}

.question-code{
	font-style: italic;
	font-weight: normal;
	white-space: nowrap;
}

.question-code::before{
	content: "["
}

.question-code::after{
	content: "]"
}

.question-text{
	order: 2;
}

/* end general styling of questions */

.boilerplate .ls-questionhelp{
	margin-top: 15px;
	margin-bottom: 15px;
}


.slider-handle{
	background-color: $color1;
}

.multiple-opt-comments{
	.form-horizontal .control-label{
		text-align: left;
	}
}

.array-multi-flexi{
	.ls-answers thead th{
		font-size: 80%;
		font-weight: normal; 
	}
}

.input-group-addon.btn-primary{
	color: #fff;
}

.slider.slider-untouched .slider-handle {
	background-color: transparent;
}

.slider-handle.custom::before{
	color: $color1;
}

.slider.slider-untouched .slider-handle.custom::before{
	color: #bbb;
}