@import 'variables';
@import 'privacy';
@import 'overrides';
@import 'questions'; 
@import 'buttons';
@import 'tables';
@import 'group';
@import 'radio';
@import 'checkboxes';
@import 'styling';

#navbar{
	font-size: 14px;
}

h1, h2, h3, h4, h5,
.h1, .h2, .h3, .h4, .h5, .h6 {
  color: $color1;
	font-style: normal;
	font-weight: normal;
}

h1, .jumbotron h1, #wrapper h1 {
	font-size: 2.45rem;
	line-height: 3.3rem;
}

#welcome-container p{
	margin: 0 0 1.4rem;
	line-height: 2rem;
}

#welcome-container{
	border: 1px solid rgba(102, 102, 102, 0.35);
  background-color: rgba(255, 255, 255, 0.5);
	padding:15px;
	border-radius: 4px;
	margin-bottom: 15px;
	margin-top: 15px;
	
	.survey-description,
	.h4{
		color: #333;
	}
	
	.survey-description.text-center,
	.survey-name.text-center{
		text-align: left;
	}
	
	 h1.survey-name{
		margin-top: 0;
	}
}

.privacy > div{
	border: 1px solid rgba(102, 102, 102, 0.35);
  background-color: rgba(255, 255, 255, 0.5);
	padding:15px;
	border-radius: 4px;
	margin-bottom: 15px;
	margin-top: 15px;
}

.top-container{
	.progress{
		height: 10px;
		box-shadow: none;
	}
}

.progress-bar{
	background-color: $color1;
	color: $color1;
	box-shadow: none;
	border: none;
	font-size: 1px;
}

.progress{
	border: 1px solid rgba(102, 102, 102, 0.35);
  background-color: rgba(255, 255, 255, 0.5);
}

.language_change_container{
	height: auto;
	overflow: hidden !important;
}